<template>
  <div id="setting">
    <div class="goback" @click="goBack">
      <p @click="history.back()"> 뒤로가기</p>
    </div>
    <div class="texts">
      <h2>학교 설정</h2>
      <p>학급 시간표를 제공하기 위한</p>
      <p>필수 정보를 입력해주세요.</p>
      <br/>
    </div>

    <transition name="fade" appear>
      <div class="modal-overlay" v-if="selected!=null" @click="selected=null"></div>
    </transition>
    <transition name="pop" appear>
      <div class="modal" role="dialog" v-if="selected!=null">
        <div class="page" v-if="selected==='school'">
          <SchoolSelectModal @setSchool="setSchool"/>
        </div>
        <div class="page" v-if="selected==='grade'">
          <GradeSelectModal :school_object="school_object" @setGrade="setGrade"/>
        </div>
        <div class="page" v-if="selected==='class'">
          <ClassSelectModal :school_object="school_object" :grade="school_grade" @setClass="setClass"/>
        </div>
      </div>
    </transition>

    <div class="select-bar" @click="select('school')">
      <p v-if="school_object != null">{{school_object.SCHUL_NM}}</p>
      <p v-else>학교</p>
    </div>
    <div class="select-bar" @click="select('grade')">
      <p v-if="school_grade !== null">{{school_grade}} 학년</p>
      <p v-else>학년</p>
    </div>
    <div class="select-bar" @click="select('class')">
      <p v-if="school_class !== null">{{school_class}} 반</p>
      <p v-else>반</p>
    </div>

    <div class="fin" @click="save">
      <p>저장하기</p>
    </div>
  </div>
</template>

<script>
import SchoolSelectModal from "../components/SchoolSelectModal";
import GradeSelectModal from "../components/GradeSelectModal";
import ClassSelectModal from "@/components/ClassSelectModal";
import cookiemanager from "@/assets/js/cookiemanager";

export default {
  name: "SchoolSetting",
  components: {
    SchoolSelectModal, GradeSelectModal, ClassSelectModal
  },
  data () {
    return {
      selected: null,
      school_object: null,
      school_grade: null,
      school_class: null
    }
  },
  created() {
    this.school_object = this.getSchool()
    this.school_grade = this.getGrade()
    this.school_class = this.getClass()
  },
  methods: {
    getSchool: function () {
      let sc = cookiemanager.getCookie('school_object')
      return sc==null?null:JSON.parse(sc)
    },
    getGrade: function() {
      return cookiemanager.getCookie('school_grade')
    },
    getClass: function() {
      return cookiemanager.getCookie('school_class')
    },
    setSchool: function (data) {
      this.school_object = data
      this.selected=null
    },
    setGrade: function (data) {
      this.school_grade = data
      this.selected=null
    },
    setClass: function (data) {
      this.school_class = data
      this.selected=null
    },
    select: function (type) {
      if(type === 'school'){
        this.selected = type
      }else if(type === 'grade'){
        this.selected = type
      }else if(type === 'class'){
        this.selected = type
      }
    },
    save: function () {
      cookiemanager.setCookie('school_object', JSON.stringify(this.school_object), 30)
      cookiemanager.setCookie('school_grade', this.school_grade, 30)
      cookiemanager.setCookie('school_class', this.school_class, 30)
    },
    goBack: function () {
      window.history.back()
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'SpoqaHanSansNeo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SpoqaHanSansNeo-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
#setting {
  text-align: left;
  font-family: 'SpoqaHanSansNeo-Regular',sans-serif;
}
.texts p, h2{
  margin-left: 5%;
}

.goback {
  margin-left: 0;
  height: 25px;
  width: 100%;
  border-bottom-width: thin;
  border-bottom-color: #d0d0d0;
  border-bottom-style: solid;
}
.goback p {
  padding-left: 3%;
  position: relative;
  top: -5px;
}

.select-bar {
  margin-left: 5%;
  border: 2px solid rgba(189, 189, 189);
  border-radius: 10px;
  margin-top: 3px;
  height: 50px;
  width: 90%;
}
.select-bar p{
  margin-top: 14px;
  color: black;
  margin-left: 10px;
}

.fin {
  text-align: center;
  margin-left: 5%;
  border-radius: 10px;
  position: absolute;
  right: 0;
  bottom: 1%;
  left: 0;
  height: 50px;
  width: 90%;
  background-color: #3282f6;
}

.fin p{
  margin-top: 14px;
  color: white;
}
.modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto auto 0 auto;
  text-align: center;
  width: 80%;
  height: fit-content;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
  background: #FFF;
  z-index: 999;
  transform: none;
}
.modal h1 {
  margin: 0 0 1rem;
}

.modal-overlay {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 998;
  background: #2c3e50;
  opacity: 0.6;
  cursor: pointer;
}

/* ---------------------------------- */
.fade-enter-active,
.fade-leave-active {
  transition: opacity .4s linear;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.pop-enter-active,
.pop-leave-active {
  transition: transform 0.4s cubic-bezier(0.5, 0, 0.5, 1), opacity 0.4s linear;
}

.pop-enter,
.pop-leave-to {
  opacity: 0;
  transform: translateY(-100px);
}

</style>