<template>
  <div class="school-select-modal">
    <h2>학교</h2>
    <input type="text" id="schoolname">
    <button class="search" @click="searchSchool()">검색하기</button>
    <br/>
    <div class="schools">
      <div class="school" v-for="(i,j) in searchedSchools" :key="j" @click="$emit('setSchool',i)">
        <p class="name">{{i.SCHUL_NM}}</p>
        <p class="address">{{i.ORG_RDNMA}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import neismanager from '../assets/js/neismanager'
export default {
  name: 'SchoolSelectModal',
  data () {
    return {
      searchedSchools: []
    }
  },
  methods: {
    searchSchool: function () {
      this.searchedSchools.splice(0, this.searchedSchools.length)
      neismanager.searchSchool(document.getElementById('schoolname').value, (rtv) => {
        for (let i = 0; i < rtv['schoolInfo'][1]['row'].length; i++) {
          this.searchedSchools[i] = (rtv['schoolInfo'][1]['row'][i])
        }
      })
    },
  }
}
</script>

