<template>
  <div class="grade-select-modal">
    <h2>학년</h2>
    <br/>
    <div class="grades">
      <div class="grade" v-for="(i) in getMaxGrade()" :key="i" @click="$emit('setGrade',i)">
        <p class="name">{{i}} 학년</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "GradeSelectModal",
  props: {
    school_object: Object
  },
  methods: {
    getMaxGrade: function () {
      if(this.school_object.SCHUL_KND_SC_NM === '초등학교')return 6
      return 3
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'SpoqaHanSansNeo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SpoqaHanSansNeo-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
* {
  font-family: 'SpoqaHanSansNeo-Regular', sans-serif;
}

.grade {
  margin-top: 1px;
  border: 2px solid rgba(189, 189, 189);
  border-radius: 10px;
  height: 50px;
  margin-left: 3%;
  width: 94%;
}
.grade p {
  margin-top: 14px;
}
</style>