<template>
  <div class="class-select-modal">
    <h2>반</h2>
    <br/>
    <div class="classes" v-if="maxGrade != null">
      <div class="school-class" v-for="(i) in maxGrade" :key="i" @click="$emit('setClass',i)">
        <p class="name">{{i}} 반</p>
      </div>
    </div>
  </div>
</template>

<script>
import neismanager from "@/assets/js/neismanager";

export default {
  name: "ClassSelectModal",
  props: {
    school_object: Object,
    grade: Number
  },
  data () {
    return {
      maxGrade: null
    }
  },
  created() {
    this.getMaxClass()
  },
  methods: {
    getMaxClass: function () {
      neismanager.getMaxClass(this.school_object, this.grade, m => {
        this.maxGrade = m
      })
    }
  }
}
</script>

<style scoped>
@font-face {
  font-family: 'SpoqaHanSansNeo-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SpoqaHanSansNeo-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
* {
  font-family: 'SpoqaHanSansNeo-Regular', sans-serif;
}

.school-class {
  margin-top: 1px;
  border: 2px solid rgba(189, 189, 189);
  border-radius: 10px;
  height: 50px;
  margin-left: 3%;
  width: 94%;
}
.school-class p {
  margin-top: 14px;
}
</style>